import React, { useContext } from 'react'
import { graphql } from 'gatsby'
import ReactHTMLParser from 'react-html-parser'
import { Remarkable } from 'remarkable'
import Layout from './../layout/index'
import { SEO, StripeBar, Banner, Box, Circle, Accordion } from './../components'
import { Content, ContentContainer, Plus } from './../containers/Terms/style'
import { useLocation } from '@reach/router'

interface TermsPageProps {
  location: {
    pathname: string
  }
  pageContext: {
    language: string
    description: string
    title: string
    s2Description: string
    s2MoreDescription: string
    lesbianDescription: string
    gayDescription: string
    bisexualDescription: string
    transgenderDescription: string
    queerDescription: string
    moreDescription1: string
    moreDescription2: string
    seoKeyword: string
    seoDescription: string
  }
}

export default ({
  location,
  pageContext: {
    description,
    title,
    s2Description,
    s2MoreDescription,
    lesbianDescription,
    gayDescription,
    bisexualDescription,
    transgenderDescription,
    queerDescription,
    moreDescription1,
    moreDescription2,
    seoKeyword,
    seoDescription,
  },
}: TermsPageProps) => {
  const md = new Remarkable()
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  return (
    <Layout location={location}>
      <SEO
        pathname={location.pathname}
        description={seoDescription}
        keyword={seoKeyword}
        title={isFr ? 'Termes importants' : 'Key Terms'}
      />
      <Banner colors={['#BF340B', '#DC8336', '#DA5027']}>
        <h1>{isFr ? 'Termes importants' : 'Key Terms'}</h1>
      </Banner>
      <StripeBar colors={['#00587F', '#80D0DB', '#5A9DB3', '#95C5BB']} />
      <Content>
        <ContentContainer className='container'>
          <h2 className='text-center'>{title}</h2>
          <div className='col-md-6 mx-auto text-center'>{description}</div>
          <Box className='mt-5 d-flex flex-wrap'>
            <div className='row'>
              <div className='col-md-6'>
                <Circle className='mx-auto'>2s</Circle>
                <div className='mt-5'>
                  {ReactHTMLParser(md.render(s2Description))}
                </div>
                <Accordion
                  data={[
                    {
                      title: isFr
                        ? 'En savoir plus sur 2S'
                        : 'Read more about 2S',
                      content: s2MoreDescription,
                    },
                  ]}
                />
              </div>
              <div className='col-md-6 mt-5 mt-md-0'>
                <Circle className='mx-auto' colors={['#DA5027', '#C73E15']}>
                  L
                </Circle>
                <div className='mt-5'>
                  {ReactHTMLParser(md.render(lesbianDescription))}
                </div>
              </div>
              <div className='col-md-6 mt-5'>
                <Circle className='mx-auto' colors={['#4B3E2B', '#8C704E']}>
                  G
                </Circle>
                <div className='mt-5'>
                  {ReactHTMLParser(md.render(gayDescription))}
                </div>
              </div>
              <div className='col-md-6 mt-5'>
                <Circle className='mx-auto' colors={['#00587F', '#387896']}>
                  B
                </Circle>
                <div className='mt-5'>
                  {ReactHTMLParser(md.render(bisexualDescription))}
                </div>
              </div>
              <div className='col-md-6 mt-5'>
                <Circle className='mx-auto' colors={['#DA5027', '#C73E15']}>
                  T
                </Circle>
                <div className='mt-5'>
                  {ReactHTMLParser(md.render(transgenderDescription))}
                </div>
              </div>
              <div className='col-md-6 mt-5'>
                <Circle className='mx-auto' colors={['#4B3E2B', '#8C704E']}>
                  Q
                </Circle>
                <div className='mt-5'>
                  {ReactHTMLParser(md.render(queerDescription))}
                </div>
              </div>

              <div className='col-md-12 mt-5'>
                <Circle className='mx-auto' colors={['#00587F', '#387896']}>
                  <Plus>+</Plus>
                </Circle>
                <div className='row mt-5'>
                  <div className='d-flex flex-wrap'>
                    <div className='col-md-6'>
                      <div>{ReactHTMLParser(md.render(moreDescription1))}</div>
                    </div>
                    <div className='col-md-6'>
                      <div>{ReactHTMLParser(md.render(moreDescription2))}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </Box>
        </ContentContainer>
      </Content>
      <StripeBar colors={['#80D0DB', '#95C5BB', '#E58630', '#DA5027']} />
    </Layout>
  )
}

export const termsPageQuery = graphql`
  query TermsPageQuery {
    site {
      siteMetadata {
        title
        description
      }
    }
  }
`
